:root {
  font-size: 16px;
  font-family: "Open Sans";
  --text-primary: #b6b6b6;
  --text-secondary: #ececec;
  --bg-primary: #23232e;
  --bg-secondary: #141418;
  --transition-speed: 600ms;
  color: var(--text-primary);
}

body {
  background-color: #17141d;
  margin: 0;
  padding: 0;
}

main {
  margin-left: 5rem;
}

.title {
  margin: 1rem 0.5rem;
}

.content {
  text-indent: 1rem;
}

/*
  Scroll bar
*/
body::-webkit-scrollbar {
  width: 0.25rem;
}

body::-webkit-scrollbar-track {
  background: #1e1e24;
}

body::-webkit-scrollbar-thumb {
  background: #6649b8;
}

@media only screen and (max-width: 600px) {
  main {
    margin: 0;
  }
}
